.hidden {
  display: none;
}

.breed-select {
  margin-bottom: 20px;
}

.loader {
  width: 16px;
  height: 16px;
  color: #fff;
  background: currentColor;
  border-radius: 50%;
  position: relative;
  right: -50px;
  box-shadow: 32px 0, -32px 0, 64px 0;
}

.loader:after {
  content: "";
  width: 16px;
  height: 16px;
  background: #ff3d00;
  border-radius: 10px;
  animation: move 3s linear infinite alternate;
  position: absolute;
  top: 0;
  left: -32px;
}

@keyframes move {
  0%, 5% {
    width: 16px;
    left: -32px;
  }

  15%, 20% {
    width: 48px;
    left: -32px;
  }

  30%, 35% {
    width: 16px;
    left: 0;
  }

  45%, 50% {
    width: 48px;
    left: 0;
  }

  60%, 65% {
    width: 16px;
    left: 32px;
  }

  75%, 80% {
    width: 48px;
    left: 32px;
  }

  95%, 100% {
    width: 16px;
    left: 64px;
  }
}

